___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: "montserrat", system-ui, -apple-system, "Neue", sans-serif;
}

.container {
  display: grid;

  grid-template-columns: 1fr 4fr;

  min-height: 100vh;
}
.side-bar {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  background-color: #0b132b;
  color: #6fffe9;
}

.logo {
  font-size: 3rem;
  padding: 3rem 7rem;
}
.projects-header {
  font-size: 2rem;
}

.project-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  gap: 1rem;
  width: 300px;
}

.project-wrapper div {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  border: none;
  background-color: transparent;
  color: #6fffe9;
  width: 100%;
  border: 1px solid #1c2541;
  padding: 0.5rem;
}

.project-wrapper div:hover {
  background-color: #1c2541;
}

.project-wrapper button {
  background-color: transparent;
  border: none;
  color: #6fffe9;
  width: 100%;
  cursor: pointer;
}

.remove-project {
  width: 24px;
}

.create-project-btn {
  display: flex;
  justify-content: center;
  gap: 5px;
  align-items: center;
  cursor: pointer;
}

.create-project-btn > :first-child {
  width: 2rem;
}

.hero-todo-container {
  background-color: #1c2541;
  padding: 3rem 7rem;
  color: #6fffe9;
}

.hero-todo-nav {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
}

.project-name {
  font-size: 3rem;
}

.hero-todo-nav .add-task {
  max-width: 3.5rem;
}

.project-todo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.todo-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  border-bottom: 2px solid;
}

.todo-wrapper img {
  max-width: 24px;
}

.todo-wrapper input[type="checkbox"] {
  appearance: none;
  border: 2px solid #6fffe9;
  border-radius: 20px;
  width: 1rem;
  height: 1rem;
  cursor: pointer;
}

.todo-wrapper input[type="checkbox"]:checked {
  background-color: #6fffe9;
}

.title-wrapper {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.date-wrapper {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  min-width: max-content;
}

.buttons-wrapper {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.date-btn-wrapper {
  display: flex;
  align-items: center;
  gap: 15rem;
}

.dialog-project,
.dialog-add-todo {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #0b132b;
  color: #6fffe9;
}

.dialog-project {
  height: 25vh;
  width: 25vw;
}
.dialog-add-todo {
  height: 45vh;
  width: 40vw;
}

.dialog-project form,
.dialog-add-todo form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
}

.close-dialog {
  position: absolute;
  right: 10px;
  top: 5px;
  color: #6fffe9;
  cursor: pointer;
}

.dialog-project label,
.dialog-add-todo label {
  display: block;
}
.dialog-project input,
.dialog-add-todo input,
.dialog-add-todo select {
  appearance: none;
  outline: none;
  border: none;
  width: 100%;
  background-color: #1c2541;
  color: #6fffe9;
}

.dialog-project input:focus,
.dialog-add-todo input:focus {
  border: 1px solid #6fffe9;
}

.submit-dialog-project {
  background-color: #6fffe9;
  border: none;
}
